











import { Component, Vue } from 'vue-property-decorator'

const ErrorProps = Vue.extend({
  props: {
    text: String,
    default: () => '',
  },
})

@Component
export default class ErrorText extends ErrorProps {
  get errorText(): string {
    return this.text
  }

  refresh() {
    this.$router.go(0)
  }
}
