


















































































import { Component, Vue } from 'vue-property-decorator'
import { checkEmailValidation } from '@/utils/validation'
import AppButton from '@/components/UI/AppButton.vue'
import AppInput from '@/components/UI/AppInput.vue'
import InfoError from '@/components/info/InfoError.vue'
import { convertErrorToString } from '@/utils/convert'

const SignupAdminFormProps = Vue.extend({
  props: {
    validEmail: String,
    signupCb: Function,
  },
})

@Component({
  components: {
    AppInput,
    AppButton,
    InfoError,
  },
})
export default class SignupAdminForm extends SignupAdminFormProps {
  emailVerificationInfo = {
    type: 'success',
    text: 'Email is validated',
  }
  passwordVerificationInfo = {
    type: '',
    text: '',
  }
  passwordConfirmInfo = {
    type: '',
  }
  isLoading = false
  adminForm = {
    email: this.validEmail,
    first_name: '',
    last_name: '',
    job_title: '',
    phone: '',
    company_name: '',
    organization_size: '',
    industry: '',
    password: '',
  }
  passwordConfirm = ''

  errorText = ''

  submitButtonHandler() {
    this.emailValidation()
    this.passValidation()

    const validationSuccess =
      this.emailVerificationInfo.type === 'success' &&
      !this.passwordVerificationInfo.type &&
      !this.passwordConfirmInfo.type

    if (validationSuccess) {
      this.register()
    }
  }

  async register() {
    try {
      this.isLoading = true

      const res = await this.$store.dispatch(
        'Signup/signupAdminAction',
        this.adminForm
      )

      if (res && res.status === 204) {
        this.signupCb()
      } else {
        this.errorText = 'Ooops! Something went wrong. Please try again later'
      }
    } catch (error) {
      this.errorText = convertErrorToString(error)
      throw new Error()
    } finally {
      this.isLoading = false
    }
  }

  emailValidation() {
    const isEmailValid = checkEmailValidation(this.adminForm.email.trim())
    if (!isEmailValid) {
      this.emailVerificationInfo = {
        type: 'error',
        text: "Email wasn't validated",
      }
    } else {
      this.emailVerificationInfo = {
        type: 'success',
        text: 'Email is validated',
      }
    }
  }

  passValidation() {
    if (this.adminForm.password.length < 6) {
      this.passwordVerificationInfo = {
        type: 'error',
        text: 'Password length must be greater than 6 characters',
      }
    } else {
      this.passwordVerificationInfo = {
        type: '',
        text: '',
      }
      const isPassValid = this.adminForm.password === this.passwordConfirm

      this.passwordVerificationInfo = isPassValid
        ? {
            type: '',
            text: '',
          }
        : {
            type: 'error',
            text: 'The password confirmation does not match',
          }

      this.passwordConfirmInfo = isPassValid
        ? {
            type: '',
          }
        : {
            type: 'error',
          }
    }
  }
}
