




import { Component, Vue } from 'vue-property-decorator'

const AppLoaderProps = Vue.extend({
  props: {
    isLoading: {
      type: Boolean,
      default: () => false,
    },
  },
})

@Component({
  name: 'AppLoader',
})
export default class AppLoader extends AppLoaderProps {
  get loader(): boolean {
    return this.isLoading
  }
}
