














import { Component, Vue } from 'vue-property-decorator'

const PopupProps = Vue.extend({
  props: {
    value: Boolean,
    default: () => false,
  },
})

@Component({})
export default class AppPopup extends PopupProps {
  get isPopupOpen() {
    return this.value
  }

  closePopup() {
    this.$emit('input', false)
  }
}
